import * as React from "react";
import { Toast } from 'antd-mobile'
import qs from 'qs'

import BottomBtn from '../../components/btn/bottomBtn'
import { getCertificate } from '../../api/consumer'



import './consumer.less'
interface ISprops {
    [propsName: string]: any
}
interface IState {
    certData: any
}

export default class Certificate extends React.Component<ISprops, IState>{
    constructor(props: ISprops) {
        super(props);
        this.state = {
            certData: {}
        }
    }
    getCertificateList() {
        let search = this.props.location.search;
        let data = qs.parse(search.substring(1));
        let params = {
            courseId: data.course_id,
            memberId: data.member_id
        }

        getCertificate(params).then((data) => {

            if (!data.status) {
                this.setState({
                    certData: data.data || {}
                })
            } else {
                Toast.fail(data["message"],2)
                this.props.history.go(-1)
            }
        })

    }
    checkCert = () => {
        let search = this.props.location.search;
        let data = qs.parse(search.substring(1));
        const { certData } = this.state;
        this.props.history.push(`/consumer/getCertificate?course_id=${data.course_id}&member_id=${data.member_id}&title=${certData.real_title}`)

    }
    componentWillMount() {
        document.title = '领取证书';
        this.getCertificateList()
    }
    render() {
        const { certData } = this.state;
        return (
            <div className="certificate">
                <div className="certificate-con" style={{backgroundImage:`url(${certData.image_url})`,backgroundSize:"100%"}}>
                    {/* <img src={certData.image_url} alt=""/> */}
                    <p className="name">{certData.nickname}</p>
                    <p className="text1">{certData.award_text1}</p>
                    <p className="text2">{certData.award_text2}</p>
                    <p className="text3">{certData.award_text3}</p>
                    <span>{certData.award_date}</span>
                </div>
                {
                    certData.is_apply && certData.is_apply === 1 ?
                        <div></div>
                        :
                        <BottomBtn title="申请纸质证书" click={this.checkCert}/>
                }
            </div>
        )
    }

}
